import React from "react";
import styled from "styled-components";

const Wrapper = styled.div`
  text-align: center;
  display: inline-block;
  box-shadow: 0 0 7px #d1d1d1;
  padding: 25px;
  border-radius: 5px;
  margin: 0 25px 25px 0;
  overflow: auto;
`

const Left = styled.div`
  float: left;
`

const Right = styled.div`
  float: right;
  text-align: left;
`

const SubjectHeader = styled.div`
  padding-top: 30px;
  padding-bottom: 30px;
  font-size: 23px;
  line-height: 20px;
  font-weight: bold;
  padding-left: 10px;
  float: left;
`

const TopWrapper = styled.div`
  float: top;
  display: block;
  overflow: auto;
`

const BottomWrapper = styled.div`
  float: bottom;
  text-align: left;
`

const SubjectImage = styled.img`
  width: 80px;
`

const ItemWrapper = styled.div`
  display: block;
`


const Subject = props => {
  let list = props.list.map(item => {
    return <ItemWrapper>{item}<br/></ItemWrapper>
  })
  return (
      <Wrapper>
        <TopWrapper>
          <Left>
            <SubjectImage src={props.src}/>
          </Left>
          <Right>
            <SubjectHeader>{props.name}</SubjectHeader>
          </Right>
        </TopWrapper>
        <br/>
        <BottomWrapper>
          {list}
        </BottomWrapper>

      </Wrapper>
  )
}

export default Subject